<template>
  <form class="input-block-wrapper" @submit.prevent="submit">
    <div 
      class="input-block" 
      v-for="(block, index) in blocks" :key="index"
    >
      <span class="input-block__title">
        {{ block.title }}
      </span>
      <b-form-input
        class="input-block__input"
        v-model="$v[block.model].$model"
        :placeholder="block.placeholder ? block.placeholder : ''"
        :type="block.type ? block.type : 'text'"
        v-mask="block.mask ? block.mask : customToken"
        :state="$v[block.model].$dirty ? !$v[block.model].$error : null"
        :aria-describedby="`input-block__invalid-feedback-${block.model}`"
      />
      <b-form-invalid-feedback
        :id="`input-block__invalid-feedback-${block.model}`"
      >
        {{ block.error }}
      </b-form-invalid-feedback>
    </div>
    <button
      type="submit" :class="{ invalid: $v.$invalid }"
      class="button input-block__submit"
    >
      <span class="input-block__submit-text">Далее </span>
      <svg
        class="input-block__submit-svg"
        width="27" height="16"
        viewBox="0 0 27 16" fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M1 7C0.447715 7 0 7.44772 0 8C0 8.55228 0.447715 9 1 9V7ZM26.7071 8.70711C27.0976 8.31658 27.0976 7.68342 26.7071 7.29289L20.3431 0.928932C19.9526 0.538408 19.3195 0.538408 18.9289 0.928932C18.5384 1.31946 18.5384 1.95262 18.9289 2.34315L24.5858 8L18.9289 13.6569C18.5384 14.0474 18.5384 14.6805 18.9289 15.0711C19.3195 15.4616 19.9526 15.4616 20.3431 15.0711L26.7071 8.70711ZM1 9H26V7H1V9Z"
        />
      </svg>
    </button>
    <br />
  </form>
</template>

<script>
// import { required, minLength, email, } from 'vuelidate/lib/validators';
import { mask, } from 'vue-the-mask';

export default {
  name: "SignUp1Inputs",
  // directives: {
  //   mask: {
  //     ...mask,
  //     tokens: {
  //       ...mask.tokens,
  //         '*': /./,
  //       },
  //   },
  // },
  data() {
    return {
      customToken: {
        mask: {
          ...mask,
          tokens: {
            ...mask.tokens,
              '*': /./,
            },
        },
      },
      blocks: [
        {
          title: "Фамилия *",
          model: "lastName",
          error: "Обязательное поле, минимум 4 символа.",
        },
        {
          title: "Имя *",
          model: "firstName",
          error: "Обязательное поле, минимум 4 символа.",
        },
        {
          title: "Отчество *",
          model: "patronymic",
          error: "Обязательное поле, минимум 4 символа.",
        },
        {
          title: "Телефон *",
          type: "tel",
          mask: "+7 (###) ###-##-##",
          placeholder: "+7 (999) 999-99-99",
          model: "phone",
          error: "Обязательное поле, только цифры.",
        },
        { 
          title: "Email", 
          // type: "email",
          model: "email", 
          error: "Невалидный email.", 
        },
      ],
      lastName: null,
      firstName: null,
      patronymic: null,
      phone: null,
      email: null,
      errors: [],
    };
  },
  validations: {
    firstName: {
      // required,
      // minLength: minLength(4)
    },
    lastName: {
      // required,
      // minLength: minLength(4),
    },
    patronymic: {
      // required,
      // minLength: minLength(4)
    },
    phone: {
      // type: Number,
      // required,
      // minLength: minLength(4)
    },
    email: {
      // email,
    },
  },
  methods: {
    submit() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        this.$emit("next", "SignUp2");
      }
    },
    patternPhone(model, initial) {
      if (model == "phone") {
        initial = initial.replace(
          /[^0-9]/,
          initial.substring(0, initial.length - 2)
        );
        initial = initial.replace(/[0-9]{3}$/, " - ");
        console.log(initial);
        return initial;
      } else {
        return initial;
      }
    },
  },
};
</script>

<style scoped lang="scss">
.button:hover,
.button:hover .input-block__submit-svg {
  color: darken($color: $color-light, $amount: 40);

  fill: darken($color: $color-light, $amount: 40);
  transition: 0.1s all ease-in-out;
}

.input-block {
  &-wrapper {
    @include input;
  }

  &__submit {
    background: transparent;
    border: 0;
    margin-left: 155px;
    margin-top: 69px;
    margin-bottom: 78px;

    color: $color-pre-light;
    font-family: Play;
    font-style: normal;
    font-weight: normal;
    font-size: 36px;
    line-height: 144.2%;
    text-align: center;

    user-select: none;

    &-svg {
      fill: $color-light;
    }

    .invalid {
      color: $color-medium;

      pointer-events: none;
    }
  }
}

.form-control::placeholder {
  color: $color-pre-medium;
  opacity: 1;
}
</style>